@import url('https://fonts.googleapis.com/css?family=Open+Sans|Quicksand:300&display=swap');

body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
    font-family: 'Quicksand', sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container { position: absolute; top: 0; left: 0; width: 100%; height: 100vh;
  background-size: cover; } 

.clouds { position: absolute; top: 0; left: 0; width: 100%; height: 100%; background: url("img/cloud.png");
  animation: animate 60s linear infinite; } 
  @keyframes animate 
  { 
  0% { background-position: 0px; } 
  100% { background-position: -5440px; } }
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
