table{
    /* border: 10; */
    /* background-color: burlywood; */
    border-color: black; 
    border-spacing:30px ;
    min-height: 45vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}
tbody{
    padding-left: 1em;
    text-align: center;
}
tr{
    /* padding-right: 30px; */
    text-align: center;
}
td{
    /* padding-bottom: 100px; */
    padding: 0em 2em 0em 2em;
}
tr a{
    text-decoration: none;
    /* color: #cd5c5c; */
}
/* tr :hover{
    color: #cd5c5c;
} */
h1{
    margin: 0px;
    padding-top: 3em;
}

.topPart{

    background-image: url("/img/test1.jfif");
    /* color: #0290D9 ; */
    color: #FFFFFF;
    text-shadow: 2px 2px 5px #5c6968;
}
.fall{
    color: #FFAC81;
    text-shadow: 2px 2px 5px #4f5554;
    font-weight: bolder;
}
.kinaxis{
    color: #da1f2f;
    text-shadow: 2px 2px 5px #4f5554;
    font-weight: bolder;
}
.name{
    color: #58CC02;
    color:#f27f3d;
    text-shadow: 2px 2px 5px #4f5554;
    font-weight: bolder;
}

.table-foot{
    color:rgba(238, 226, 218, 0.794);

}

.table-foot:hover{
    color:white;
    text-shadow: 0 0 3px white;
}

.contact-title:hover{
    color:white;
}

