@-webkit-keyframes AnimationName {
  0%{background-position:0% 57%, center}
  50%{background-position:100% 44%, center}
  100%{background-position:0% 57%, center}
}
@keyframes AnimationName {
  0%{background-position:0% 57%, center}
  50%{background-position:100% 44%, center}
  100%{background-position:0% 57%, center}
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

